<!-- eslint-disable -->
<template>
  <div id="popup">
    <div class="close-btn" @click="close()">
      <div class="icon">
        <div class="line"></div>
        <div class="line"></div>
      </div>
    </div>
    <div class="content">
      <div class="subtitle">Formação em destaque</div>
      <div class="title">{{title}}</div>
      <div class="date">De dia {{date_start}} a {{date_end}}</div>
      <router-link :to="url" target="_blank">
        <div class="cta">
          <div class="btn">
            <div class="txt">Saber mais</div>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";

export default {
  data() {
    return {
      title: "",
      date_start: "",
      date_end: "",
      url: "",
    };
  },
  methods: {
    close() {
      $("#popup").removeClass("active");
    },
  },
  mounted() {
    var vm = this;
    var map = Array.prototype.map;

    axios
      .get("https://www.anacarolinapereira.pt/db-api/getFormacaoInfo.php")
      .then((resp) => {
        const data = resp.data;
        var info = [];

        map.call(data, function (d) {
          if (d.is_destaque == 1) {
            vm.$data.title = d.title;

            var date_elems_start = d.date_start.split("-");
            var year_start = date_elems_start[0];
            var month_start = date_elems_start[1];
            var day_start = date_elems_start[2];

            switch (month_start) {
              case "01":
                month_start = "Janeiro";
                break;
              case "02":
                month_start = "Fevereiro";
                break;
              case "03":
                month_start = "Março";
                break;
              case "04":
                month_start = "Abril";
                break;
              case "05":
                month_start = "Maio";
                break;
              case "06":
                month_start = "Junho";
                break;
              case "07":
                month_start = "Julho";
                break;
              case "08":
                month_start = "Agosto";
                break;
              case "09":
                month_start = "Setembro";
                break;
              case "10":
                month_start = "Outubro";
                break;
              case "11":
                month_start = "Novembro";
                break;
              case "12":
                month_start = "Dezembro";
                break;
              default:
                month_start = "undefined";
                break;
            }

            vm.$data.date_start =
              day_start + " de " + month_start + " de " + year_start;

            var date_elems_end = d.date_end.split("-");
            var year_end = date_elems_end[0];
            var month_end = date_elems_end[1];
            var day_end = date_elems_end[2];

            switch (month_end) {
              case "01":
                month_end = "Janeiro";
                break;
              case "02":
                month_end = "Fevereiro";
                break;
              case "03":
                month_end = "Março";
                break;
              case "04":
                month_end = "Abril";
                break;
              case "05":
                month_end = "Maio";
                break;
              case "06":
                month_end = "Junho";
                break;
              case "07":
                month_end = "Julho";
                break;
              case "08":
                month_end = "Agosto";
                break;
              case "09":
                month_end = "Setembro";
                break;
              case "10":
                month_end = "Outubro";
                break;
              case "11":
                month_end = "Novembro";
                break;
              case "12":
                month_end = "Dezembro";
                break;
              default:
                month_end = "undefined";
                break;
            }

            vm.$data.date_end =
              day_end + " de " + month_end + " de " + year_end;
            vm.$data.url = d.link;
          }
        });
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(130);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

#popup {
  background-color: #fff;
  min-width: pxToEm(400);
  width: auto;
  height: auto;
  position: fixed;
  z-index: 99999999;
  bottom: pxToEm(20);
  left: -50%;
  box-shadow: pxToEm(1) pxToEm(-1) pxToEm(11) rgba(0, 0, 0, 0.2);
  border-radius: pxToEm(10);
  padding: pxToEm(15);
  transition: 1s;

  &.active {
    left: pxToEm(20);
  }

  .close-btn {
    border-radius: 50%;
    width: pxToEm(14);
    height: pxToEm(14);
    background-color: #0babc5;
    position: absolute;
    right: pxToEm(5);
    top: pxToEm(5);
    cursor: pointer;

    .icon {
      height: 100%;
      width: 100%;

      > .line {
        height: pxToEm(2);
        width: pxToEm(8);
        background-color: #fff;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);

        &:first-child {
          transform: translateX(-50%) translateY(-50%) rotate(45deg);
        }

        &:last-child {
          transform: translateX(-50%) translateY(-50%) rotate(-45deg);
        }
      }
    }
  }

  .content {
    margin-top: pxToEm(30);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    > div {
      margin-bottom: pxToEm(15);
    }

    .title {
      font-size: pxToEm(20);
      color: #0babc5;
    }

    .cta {
      width: pxToEm(185);
      height: pxToEm(50);
      border-radius: pxToEm(10);
      background-color: #0babc5;
      margin: pxToEm(15) 0;
      cursor: pointer;

      &:focus {
        outline: none;
      }

      .btn {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;

        .txt {
          color: #fff;
          text-transform: uppercase;
        }
      }
    }
  }
}
</style>



